let map, marker, infowindow, addres

function initMap() {
    addres = { lat: 30.345875, lng: -9.506139 };
    let latc = addres.lat + 0.0005
    let lngc = addres.lng + 0.001
    let centerMap = { lat: latc, lng: lngc }

    map = new google.maps.Map(document.getElementById('map'), {
        center: centerMap,
        zoom: 17,
        mapTypeControl: false,
        streetViewControl: false,
        zoomControl: false,
        fullscreenControl: false,
        // gestureHandling: 'greedy',
    });
    $.getJSON("./js/mstyle.json", function (data) {
        map.setOptions({ styles: data });
    });

    marker = new google.maps.Marker({
        position: addres,
        map: map,
        icon: {
            url: 'img/marker.svg',
            scaledSize: new google.maps.Size(50, 50)
        },
        animation: google.maps.Animation.DROP
    });
    google.maps.event.addListenerOnce(map, 'idle', function () {
        setInterval(function () {
            $('img[src="img/marker.svg"]').css({
                'transform': 'rotate(' + 90 + 'deg)'
            });
        }, 1);
    });
}